import React, { Fragment } from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Noze from "../images/card_images/Noze.jpg";
import Nuzky from "../images/card_images/Nuzky.jpg";
import DlataImg from "../images/card_images/Dlata.jpg";
import Ostatni from "../images/card_images/Ostatni.jpg";



class Rucni extends React.Component {
    constructor () {
        super();
        this.state = {
            showNoze: false,
            showNuzky: false, 
            showDlaticka: false,
            showOstatni: false,
        }
    }

    // functions

    handleModalNoze() {
        this.setState({ showNoze: !this.state.showNoze });
        }

    handleModalNuzky() {
        this.setState({ showNuzky: !this.state.showNuzky });
        }

    handleModalDlata() {
        this.setState({ showDlaticka: !this.state.showDlaticka });
        }
    
    handleModalOstatni() {
        this.setState({ showOstatni: !this.state.showOstatni });
        }
    


    render() {
    return (

        <Fragment>
            <a id="RucniNastroje"></a>
            <h3 className="Kapitola-yellow ">Broušení ručních nástrojů</h3>
            <div className="CardGroup yellow-background">
            
                    <Card className="BombCardGroup">
                    <Card.Img src={Noze} className="BombCardImg"/>
                    <Card.Body className="gray-background">
                        <Card.Title>Nože</Card.Title>
                        <Card.Text className="BombText">- běžné i značkové kuchyňské nože</Card.Text>
                        <Card.Text className="BombText">- profesionální kuchařské a řeznické nože</Card.Text>
                        <Card.Text className="BombText">- zavírací nože, lovecké a speciální nože</Card.Text>
                    </Card.Body>
                    <Card.Footer className="gray">
                        <Button size="lg" variant="danger" onClick={() => {this.handleModalNoze()}}>Detail</Button>
                    </Card.Footer>
                    </Card>
            
                    <Card className="BombCardGroup">
                    <Card.Img src={Nuzky} className="BombCardImg"/>
                    <Card.Body className="gray-background">
                        <Card.Title>Nůžky</Card.Title>
                        <Card.Text className="BombText">- kancelářské</Card.Text>
                        <Card.Text className="BombText">- krejčovské</Card.Text>
                        <Card.Text className="BombText">- zahradnické</Card.Text>
                        <Card.Text className="BombText">- nůžky na plech</Card.Text>
    
                    </Card.Body>
                        <Card.Footer className="gray">
                            <Button size="lg" variant="danger" onClick={() => {this.handleModalNuzky()}}>Detail</Button>
                        </Card.Footer>
                    </Card>

                    <Card className="BombCardGroup">
                    <Card.Img src={DlataImg} className="BombCardImg"/>
                    <Card.Body className="gray-background">
                        <Card.Title>Dláta</Card.Title>
                        <Card.Text className="BombText">- truhlářská</Card.Text>
                        <Card.Text className="BombText">- řezbářská</Card.Text>
                        <Card.Text className="BombText">- soustružnická</Card.Text> 
                    </Card.Body>
                        <Card.Footer className="gray">
                            <Button size="lg" variant="danger" onClick={() => {this.handleModalDlata()}}>Detail</Button>
                        </Card.Footer>
                    </Card>

                    <Card className="BombCardGroup">
                    <Card.Img src={Ostatni} className="BombCardImg"/>
                    <Card.Body className="gray-background">
                        <Card.Title>Ostatní</Card.Title>
                        <Card.Text className="BombText">- dýky, sekáčky, mačety a sekery</Card.Text>
                        <Card.Text className="BombText">- sedlářské nástroje</Card.Text>
                        <Card.Text className="BombText">- ševcovské nástroje</Card.Text>
                        
                    </Card.Body>
                        <Card.Footer className="gray">
                            <Button size="lg" variant="danger" onClick={() => {this.handleModalOstatni()}}>Detail</Button>
                        </Card.Footer>
                    
                    </Card>
            </div>




            {/* Modals */}

            <Modal size="lg" centered className="BombModal" show={this.state.showNoze} onHide={() => this.handleModalNoze()}>
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Nože</h3></Modal.Title>
                <Modal.Body>
                    <p>
                    Brousíme běžné i značkové kuchyňské nože, a také profesionální kuchařské, řeznické a lovecké nože. U běžných nožů provádíme jemné broušení ostří pod požadovaným úhlem a obtažení na koženém pásu. U značkových a profesionálních nožů, kde je použita výrazně kvalitnější ocel, navíc provádíme i leštění ostří na koženém kotouči s diamantovou pastou.
                    </p>
                    <br/>
                    <p>
                    K broušení veškerých nožů používáme výhradně patentovanou švédskou technologii značky Tormek s pomaloběžným karbidovým kotoučem, ponořeným ve vodní lázni, a originální příslušenství značky Tormek. To pak je, ruku v ruce s nezbytnou pečlivostí, zárukou kvalitního rovnoměrně a přesně zhotoveného ostří s dlouhou životností.
                    Pro opravdové nadšence s velmi vysokými nároky nabízíme na přání i ruční broušení nožů na japonských vodních kamenech o zrnitosti (tzv. gridu) 1000 / 3000, popř. i více. 
                    </p>
                    <br/>
                    <p>
                    Lze tak dosáhnout mimořádně jemného a precizně vybroušeného žiletkového ostří.
                    Ani u sebelépe nabroušeného nože se však při jeho opakovaném používání nevyhneme postupnému zaoblování ostří. Nůž se tak pozvolna otupuje. Doporučujeme proto přibližně 1x za měsíc naostření všech používaných nožů kvalitní ocílkou. Ostří se tím takzvaně osvěží, prodlouží se tím jeho životnost, a tedy i lhůta pro další nezbytné broušení na brusce s karbidovým kotoučem. Pozor - při ostření na ocílce nesmí být nůž v žádném případě mastný.
                    </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModalNoze()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>

            <Modal size="lg" centered className="BombModal" show={this.state.showNuzky} onHide={() => this.handleModalNuzky()}>
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Nůžky</h3></Modal.Title>
                <Modal.Body>
                <p>
                Nabízíme broušení především standardních kancelářských nůžek všech velikostí. Brousíme také krejčovské a entlovací nůžky, kuchyňské nůžky na drůbež, zahradnické nůžky na růže a stromky, i dlouhé ruční nůžky na živé ploty.
                </p>
                <br/>
                <p>
                Při broušení všech typů nůžek používáme výhradně patentovanou švédskou technologii značky Tormek s pomaloběžným karbidovým kotoučem, ponořeným ve vodní lázni, a originální upínací příslušenství značky Tormek. Tím je zaručeno hladké a rovné ostří s dlouhou životností. 
                </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModalNuzky()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>
            
            <Modal size="lg" centered className="BombModal" show={this.state.showDlaticka} onHide={() => this.handleModalDlata()}>
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Dláta</h3></Modal.Title>
                <Modal.Body>
                    <p>
                    Brousíme klasická truhlářská dláta s rovným (plochým) ostřím. Dále nabízíme broušení řezbářských dlát s rovným i tvarovým ostřím s následným leštěním ostří diamantovou pastou, a poté s obtažením na rovném či tvarovém koženém kotouči. Brousíme také soustružnická dláta (tzv. struhy) s následným přebroušením čela břitu na jemném vodním kameni.
                    </p>
                    <br/>
                    <p>
                    K broušení všech typů dlát rovněž používáme výhradně patentovanou švédskou technologii značky Tormek s pomaloběžným karbidovým kotoučem a úhlovým upínacím přípravkem. Tento speciální kotouč, ponořený při broušení ve vodní lázni, a úhlový přípravek značky Tormek nám dávají možnost dosáhnout takového výsledku, jaký je na jiných ručních bruskách prakticky nedosažitelný.
                    </p>
                    <br/>
                    <p>
                    Na přání nabízíme rovněž ruční broušení rovných truhlářských a řezbářských dlát na japonských vodních kamenech o zrnitosti (tzv. gridu) 1000 / 3000 a více. Výsledné ostří je pak mimořádně jemné a hladké, čímž se následná práce s takovými dláty stává opravdovým potěšením.
                    </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModalDlata()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>
            
            <Modal size="lg" centered className="BombModal" show={this.state.showOstatni} onHide={() => this.handleModalOstatni()}>
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Ostatní</h3></Modal.Title>
                <Modal.Body>
                <p>
                Brousíme také veškeré ruční nástroje, jako jsou želízka do hoblíků, kuchyňské sekáčky, sekery, mačety, dýky a další truhlářské, tesařské, sedlářské či ševcovské nástroje.
                </p>
                <br/>
                <p>
                K broušení celé škály těchto řemeslnických nástrojů také používáme výhradně patentovanou švédskou technologii značky Tormek s pomaloběžným karbidovým kotoučem, ponořeným ve vodní lázni. Spolu s originálním příslušenstvím značky Tormek a nezbytnou pečlivostí tak dosáhneme kvalitního, rovnoměrně a precizně zhotoveného ostří s dlouhou životností.
                </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModalOstatni()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>

        </Fragment>

        )
    }

}

export default Rucni