import React, { Fragment } from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Vrtaky from "../images/card_images/Vrtaky.jpg";
import Hoblo from "../images/card_images/Hoblo.png";
import Kotouce from "../images/card_images/Kotouce.jpg";
import Retezy from "../images/card_images/Retez.jpg";
import Sekace from "../images/card_images/Sekace.jpg";
// import Brusle from "../images/card_images/Brusle.jpg"



class Motorove extends React.Component {
    constructor () {
        super();
        this.state = {
            showVrtaky: false,
            showHoblo: false, 
            showKotouce : false,
            showRetezy: false,
            showSekace: false,
            showBrusle: false,
        }
    }

    // functions

    handleModalVrtaky() {
        this.setState({ showVrtaky: !this.state.showVrtaky });
        }

    handleModalHoblo() {
        this.setState({ showHoblo: !this.state.showHoblo });
        }

    handleModalKotouce() {
        this.setState({ showKotouce: !this.state.showKotouce });
        }
    
    handleModalRetezy() {
        this.setState({ showRetezy: !this.state.showRetezy });
        }
    
    handleModalSekace() {
        this.setState({ showSekace: !this.state.showSekace });
        }
        
    // handleModalBrusle() {
    //     this.setState({ showBrusle: !this.state.showBrusle });
    //     }

    render() {
    return (

        <Fragment>
            <a id="MotoroveNaradi"></a>
            <h3 className="Kapitola-yellow ">Broušení nástrojů pro motorové nářadí</h3>
            <div className="CardGroup yellow-background">
            
                    <Card className="BombCardGroup">
                    <Card.Img src={Vrtaky} className="BombCardImg"/>
                    <Card.Body className="gray-background">
                        <Card.Title>Spirálové vrtáky</Card.Title>
                        <Card.Text className="BombText">- na kov i na dřevo do Ø 40 mm</Card.Text>
                        
                    </Card.Body>
                    <Card.Footer className="gray">
                        <Button size="lg" variant="danger" onClick={() => {this.handleModalVrtaky()}}>Detail</Button>
                    </Card.Footer>
                    </Card>
            
                    <Card className="BombCardGroup">
                    <Card.Img src={Hoblo} className="BombCardImg"/>
                    <Card.Body className="gray-background">
                        <Card.Title>Hoblovací nože</Card.Title>
                        <Card.Text className="BombText">- hoblovací nože HSS až do délky 700 mm</Card.Text>
                    </Card.Body>
                    <Card.Footer className="gray">
                        <Button size="lg" variant="danger" onClick={() => {this.handleModalHoblo()}}>Detail</Button>
                    </Card.Footer>
                    </Card>

                    <Card className="BombCardGroup">
                    <Card.Img src={Kotouce} className="BombCardImg"/>
                    <Card.Body className="gray-background">
                        <Card.Title>Pilové kotouče</Card.Title>
                        <Card.Text className="BombText">- klasické pilové kotouče do Ø 700 mm</Card.Text>
                        <Card.Text className="BombText">- pilové kotouče s SK břity do Ø 700 mm</Card.Text>
                    </Card.Body>
                    <Card.Footer className="gray">
                        <Button size="lg" variant="danger" onClick={() => {this.handleModalKotouce()}}>Detail</Button>
                    </Card.Footer>
                    </Card>

                    <Card className="BombCardGroup">
                    <Card.Img src={Retezy} className="BombCardImg"/>
                    <Card.Body className="gray-background">
                        <Card.Title>Pilové řetězy</Card.Title>
                        <Card.Text className="BombText">- řetězy pro benzínové i elektrické pily</Card.Text>
                    </Card.Body>
                    <Card.Footer className="gray">
                        <Button size="lg" variant="danger" onClick={() => {this.handleModalRetezy()}}>Detail</Button>
                    </Card.Footer>
                    </Card>

                    <Card className="BombCardGroup">
                    <Card.Img src={Sekace} className="BombCardImg"/>
                    <Card.Body className="gray-background">
                        <Card.Title>Nože do sekaček a riderů</Card.Title>
                        <Card.Text className="BombText">- nože do rotačních sekaček</Card.Text>
                        <Card.Text className="BombText">- nože do riderů a křovinořezů</Card.Text>
                    </Card.Body>
                    <Card.Footer className="gray">
                        <Button size="lg" variant="danger" onClick={() => {this.handleModalSekace()}}>Detail</Button>
                    </Card.Footer>
                    </Card>

                    {/* <Card className="BombCardGroup">
                    <Card.Img src={Brusle} className="BombCardImg"/>
                    <Card.Body className="yellow-background">
                        <Card.Title>Broušení bruslí</Card.Title>
                        <Card.Text className="BombText black-color">- dámské</Card.Text>
                        <Card.Text className="BombText black-color">- pánské</Card.Text>
                        <Card.Text className="BombText black-color">- dětské</Card.Text>
                        <Card.Text className="BombText black-color">- náhradní ocelové nože k bruslím</Card.Text>
                    </Card.Body>
                    <Card.Footer className="yellow">
                        <Button size="lg" variant="danger" onClick={() => {this.handleModalBrusle()}}>Více...</Button>
                    </Card.Footer>
                    </Card> */}
            </div>




            {/* Modals */}

            <Modal size="lg" centered className="BombModal" show={this.state.showVrtaky} onHide={() => this.handleModalVrtaky()}>
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Spirálové vrtáky</h3></Modal.Title>
                <Modal.Body>
                    <p>
                    Nabízíme broušení veškerých spirálových vrtáků na kov a dřevo. Brousíme vrtáky s válcovou i kuželovou upínací stopkou, a také ploché vrtáky do dřeva. K broušení využíváme výhradně úhlově nastavitelné přípravky. Lze tak vyhovět i požadavkům na nestandardní vrcholové úhly spirálových vrtáků, či atypické tvary hlavního i příčného ostří. Vrtáky brousíme až do průměru 40 mm.
                    </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModalVrtaky()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>

            <Modal size="lg" centered className="BombModal" show={this.state.showHoblo} onHide={() => this.handleModalHoblo()}>
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Hoblovací nože</h3></Modal.Title>
                <Modal.Body>
                    <p>
                    Brousíme veškeré hoblovací nože z HSS oceli do ručních elektrických hoblíků a elektrických hoblovek. Hoblovací nože brousíme bez ohledu na jejich upínací systém – tedy typ Jet, Rojek, Roxta a další, včetně nožů s oboustranným ostřím. 
                    </p>
                    <br/>
                    <p>
                    Naše vybavení umožňuje broušení veškerých hoblovacích nožů až do délky 700 mm. Broušení provádíme velmi citlivě a s minimálním úběrem, a díky tomu nehrozí přehřívání břitu a tím pádem ani následná ztráta tvrdosti broušeného ostří.
                    </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModalHoblo()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>
            
            <Modal size="lg" centered className="BombModal" show={this.state.showKotouce} onHide={() => this.handleModalKotouce()}>
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Pilové kotouče</h3></Modal.Title>
                <Modal.Body>
                    <p>
                    Brousíme veškeré standardní pilové kotouče z nástrojové oceli s trojúhelníkovými zuby i s vlčími zuby, a to jak s pozitivním, tak i s negativním úhlem čela. Součástí broušení těchto kotoučů je i případné rozvádění pilových zubů (tzv. šraňkování). Pro pilové kotouče z nástrojové oceli používáme tvarové brusné kotouče značky Tyrolit.
                    </p>
                    <br/>
                    <p>
                    Nabízíme samozřejmě i broušení dražších a přesnějších kotoučů s pájenými břity ze slinutých karbidů (tzv. SK plátky), a to jak s rovnými, tak i se střídavými zuby. Na tyto kotouče s SK plátky používáme výhradně diamantové brusné kotouče s velmi jemným zrnem. 
                    </p>
                    <br/>
                    <p>
                    U kotoučů s SK plátky, kde je podezření na nadměrné otupení či dokonce poškození SK plátků, dokážeme nabídnout i diagnostiku pilového kotouče elektronickým mikroskopem s možností zvětšení až 200x, a to včetně digitálního měření úhlů zubu a případného snímkování poškozených zubů.
                    </p>
                    <br/>
                    <p>
                    Naše technologie umožňuje broušení pilových kotoučů o průměru od 80 mm až do 700 mm.
                    </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModalKotouce()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>
            
            <Modal size="lg" centered className="BombModal" show={this.state.showRetezy} onHide={() => this.handleModalRetezy()}>
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Pilové řetězy</h3></Modal.Title>
                <Modal.Body>
                <p>
                Nabízíme broušení veškerých pilových řetězů do elektrických a benzínových pil, a to bez ohledu na
                typ a rozměry řetězu. K broušení využíváme výhradně brusivo značky Oregon, čímž dosahujeme
                citlivého a jemného úběru s dlouhou životností broušeného ostří zubů.
                </p>
                <br/>
                <p>
                Znečištěné řetězy je nutné před broušením vyprat v odmašťovací lázni. Provádíme proto praní
                takových řetězů, a po jejich nabroušení je pak i nakonzervujeme.  
                </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModalRetezy()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>

            <Modal size="lg" centered className="BombModal" show={this.state.showSekace} onHide={() => this.handleModalSekace()}>
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Nože do sekaček a riderů</h3></Modal.Title>
                <Modal.Body>
                <p>
                    Dalšími nástroji, s jejichž broušením se na nás můžete obrátit, jsou nože do rotačních sekaček, a také
                    rotační nože do všech typů zahradních riderů, bez ohledu na jejich tvar a velikost. Součástí této
                    služby je také pečlivé vyvážení nabroušeného nože, které výrazně omezuje případné nežádoucí
                    vibrace sekačky či rideru. Brousíme také kotouče do křovinořezů s jakýmkoliv tvarem a počtem zubů.  
                </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModalSekace()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>

            {/* <Modal size="lg" centered className="BombModal" show={this.state.showBrusle} onHide={() => this.handleModalBrusle()}>
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Broušení bruslí</h3></Modal.Title>
                <Modal.Body>
                <p>
                    Brousíme veškeré pánské, dámské i dětské brusle pro krasobruslení i sport. Brousíme i náhradní
                    ocelové nože k bruslím. Na nožích vybrušujeme standardní ½“ radius žlábku, na přání pak i jiný
                    rádius, a to podle konkrétního požadavku zákazníka. Naše služba zahrnuje vybroušení radiusu žlábku
                    podle požadavku zákazníka, kontrolu souměrnosti ostří a začištění hran od ostřin.  
                </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModalBrusle()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal> */}

        </Fragment>

        )
    }

}

export default Motorove