import React, { Fragment } from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import NavDropdown from "react-bootstrap/NavDropdown";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Cenik from "../images/card_images/Cenik.png"

class Navigace extends React.Component {

    constructor () {
        super ();
        this.state = {
            show : false
        }
    }

    handleModal() {
        this.setState({ show : !this.state.show });
        }

    render () {

    return (
        <Fragment>
            <Navbar collapseOnSelect variant="dark" bg="dark" expand="lg" fixed="top">
            <Navbar.Brand href="index.html#Uvod">Úvod</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                
                {/* <Nav className="mr-auto"></Nav> */}
                <Nav className="mr-auto">
                    <Nav.Link href="index.html#Onas">O nás</Nav.Link>
                    
                    <NavDropdown title="Služby" id="basic-nav-dropdown">
                        <NavDropdown.Item href="index.html#RucniNastroje">Ruční nástroje</NavDropdown.Item>
                        <NavDropdown.Item href="index.html#MotoroveNaradi">Motorové nářadí</NavDropdown.Item>
                        <NavDropdown.Item href="index.html#BrouseniBrusli">Broušení bruslí</NavDropdown.Item>
                        <NavDropdown.Item href="index.html#Kovoobrabeni">Drobné kovoobrábění</NavDropdown.Item>
                        {/* <NavDropdown.Divider /> */}
                        <NavDropdown.Item href="index.html#PiloveRetezy">Pilové řetězy</NavDropdown.Item>
                        {/* <NavDropdown.Item href="index.html#AnchorBrusle">Brusle</NavDropdown.Item> */}
                        <NavDropdown.Item href="index.html#BombickyCO2">CO<sub>2</sub> bombičky</NavDropdown.Item>
                    </NavDropdown>
                    <Nav.Link onClick={() => {this.handleModal()}}>Ceník</Nav.Link>
                    <Nav.Link href="index.html#Kontakt">Kontakt</Nav.Link>
                    
                    {/* <Nav.Link href="index.html#AnchorKontakt">Ceník</Nav.Link> */}
                    {/* <Button variant="danger" onClick={() => {this.handleModal()}}>Ceník</Button>     */}
                </Nav>
            </Navbar.Collapse>
            
            </Navbar>

            <Modal 
                size="xl"
                className="BombModal" 
                centered show={this.state.show} 
                onHide={() => this.handleModal()}
                >
                
                <Modal.Header closeButton />
                <Modal.Title className="BombModalTitle"><h3 className="Kapitola-yellow">Ceník</h3></Modal.Title>
                <Modal.Body>
                    

                    <img className="cenik-img" src={Cenik} alt="Cenik" />

                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModal()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>

        
        </Fragment>
    )
    }
}

export default Navigace;