import React, { Fragment } from "react";
import Card from "react-bootstrap/Card"
import Button from "react-bootstrap/Button"
import Modal from "react-bootstrap/Modal"
import Soustruh from "../images/card_images/Soustruh.jpg"

class Kovo extends React.Component {
    constructor () {
        super();
        this.state = {
            show: false
        }
    }

    // functions

    handleModal1() {
        this.setState({ show: !this.state.show });
        }


    render() {
    return (

        <Fragment>
            <a id="Kovoobrabeni"></a>
            <h3 className="Kapitola-yellow ">Drobné kovoobrábění</h3>
            
            <div className="CardGroup yellow-background">
                <Card className="BombCardGroup">
                <Card.Img src={Soustruh} className="BombCardImg"/>
                <Card.Body className="gray-background">
                    {/* <Card.Title>Bomby</Card.Title> */}
                    <Card.Text className="BombText">
                    V omezené míře nabízíme drobné strojní kovoobrábění a strojní dělení materiálu.
                    </Card.Text>
                </Card.Body>
                <Card.Footer className="gray">
                    <Button size="lg" variant="danger" onClick={() => {this.handleModal1()}}>Detail</Button>
                </Card.Footer>
                </Card>
            </div>

            <Modal size="lg" centered className="BombModal" show={this.state.show} onHide={() => this.handleModal1()}>
                <Modal.Header closeButton />
                <Modal.Title className="Kapitola-yellow">Drobné kovoobrábění</Modal.Title>
                <Modal.Body>
                Nabízíme:
                <ul>
                    <li>soustružení a frézování drobných zakázkových dílů</li>
                    <li>vrtání a vyvrtávání otvorů do rozličných materiálů</li>
                    <li>řezání metrických závitů a opravy poškozených závitů</li>
                    <li>dělení materiálu strojní pásovou pilou a ohýbání profilů</li>
                    <li>stříhání o ohýbání drobných plechových dílců</li>
                </ul>
                Vzhledem k různorodosti zakázek upřednostňujeme výrobu z vlastního materiálu zákazníka.
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModal1()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>

        </Fragment>

        )
    }

}

export default Kovo