import React, { Fragment } from "react";
import Card from "react-bootstrap/Card"
import Button from "react-bootstrap/Button"
import Modal from "react-bootstrap/Modal"
import Soda from "../images/card_images/Soda.jpg"

class Bombicky extends React.Component {
    constructor () {
        super();
        this.state = {
            show: false
        }
    }

    // functions

    handleModal1() {
        this.setState({ show: !this.state.show });
        }


    render() {
    return (

        <Fragment>
            <a id="BombickyCO2"></a>
            <h3 className="Kapitola-yellow"> Plnění CO<sub>2</sub>              bombiček</h3>
            <div className="CardGroup yellow-background">
                <Card className="BombCardGroup">
                <Card.Img src={Soda} className="BombCardImg"/>
                    <Card.Body className="gray-background">
                        {/* <Card.Title>Bomby</Card.Title> */}
                        <Card.Text className="BombText">
                        Plníme tlakové lahve pro výrobníky perlivé vody Sodastream apod. za polovinu obvyklé ceny.
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer className="gray">
                        <Button size="lg" variant="danger" onClick={() => {this.handleModal1()}}>Detail</Button>
                    </Card.Footer>
                </Card>
            </div>

            <Modal centered size="lg" className="BombModal" show={this.state.show} onHide={() => this.handleModal1()}>
                <Modal.Header closeButton />
                <Modal.Title className="Kapitola-yellow">Plnění CO<sub>2 </sub> bombiček</Modal.Title>
                <Modal.Body>
                    <p>
                    Zabýváme se také opakovaným plněním zálohovaných tlakových lahví pro výrobníky perlivé vody
                    všech běžných značek, jako jsou například Soda Stream, Limo Bar, My Drynk apod. K plnění tlakových
                    lahví používáme výhradně certifikované potravinářské CO<sub>2</sub> o čistotě minimálně 99,9 %.
                    </p>
                    <br/>
                    <p>
                    U každé tlakové lahve zjišťujeme před naplněním její hmotnost, a tu pak odečteme od celkové
                    hmotnosti lahve po jejím naplnění. Následně účtujeme danou cenu za každý gram náplně, a vy tak
                    zaplatíte skutečně jen takové množství náplně, jaké v každé konkrétní lahvi opravdu je. Naše ceny za
                    novou náplň těchto lahví činí přibližně polovinu obvyklé ceny.
                    </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModal1()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>

        </Fragment>

        )
    }

}
export default Bombicky