import React, { Fragment } from "react";
import KKhobby from "../images/logos/KKHobby.png"



function Main () {

    return (
        <Fragment>
            <a id="Uvod"></a>
            <div className="main-bg">            
                <div id="bg-info">
                    <h1 id="logo-name">HOBBY SERVIS K+K</h1>
                    <img id="main-logo" src={KKhobby} alt="KKhobby"></img>
                    <ul id="main-list">
                        <li>Broušení ručních nástrojů</li>
                        <li>Broušení nástrojů pro motorové nářadí</li>
                        <li>Broušení bruslí</li>
                        <li>Drobné kovoobrábění</li>
                        <li>Prodej a opravy pilových řetězů</li>
                        <li>Plnění CO<sub>2</sub> bombiček</li>
                    </ul>
                </div>
            </div>
            
        </Fragment>

    )
}

export default Main