import React, { Fragment } from "react";
import Card from "react-bootstrap/Card"
import Button from "react-bootstrap/Button"
import Modal from "react-bootstrap/Modal"
import BrusleImg from "../images/card_images/Brusle.jpg"

class Brusle extends React.Component {
    constructor () {
        super();
        this.state = {
            show: false
        }
    }

    // functions

    handleModal1() {
        this.setState({ show: !this.state.show });
        }


    render() {
    return (

        <Fragment>
            <a id="BrouseniBrusli"></a>
            <h3 className="Kapitola-gray">Broušení bruslí</h3>
            <div className="CardGroup gray-background">
                <Card className="BombCardGroup">
                <Card.Img src={BrusleImg} className="BombCardImg"/>
                <Card.Body className="yellow-background">
                    {/* <Card.Title>Bomby</Card.Title> */}
                    <Card.Text className="BombText black-color">- dámské, pánské i dětské brusle</Card.Text>
                    <Card.Text className="BombText black-color">- náhradní ocelové nože k bruslím</Card.Text>
                </Card.Body>
                <Card.Footer className="yellow">
                    <Button size="lg" variant="danger" onClick={() => {this.handleModal1()}}>Detail</Button>
                </Card.Footer>
                </Card>
            </div>
            <Modal size="lg" centered className="BombModal" show={this.state.show} onHide={() => this.handleModal1()}>
                <Modal.Header closeButton />
                <Modal.Title className="Kapitola-yellow">Broušení bruslí</Modal.Title>
                <Modal.Body>
                    <p>
                    Brousíme veškeré pánské, dámské i dětské brusle pro krasobruslení i sport. Brousíme i náhradní
                    ocelové nože k bruslím. Na nožích vybrušujeme standardní ½“ radius žlábku, na přání pak i jiný
                    rádius, a to podle konkrétního požadavku zákazníka. Naše služba zahrnuje vybroušení radiusu žlábku
                    podle požadavku zákazníka, kontrolu souměrnosti ostří a začištění hran od ostřin.  
                </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModal1()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>

        </Fragment>

        )
    }

}

export default Brusle