import React, { Fragment } from "react";
import Card from "react-bootstrap/Card"
import Button from "react-bootstrap/Button"
import Modal from "react-bootstrap/Modal"
import Retez from "../images/card_images/Oregon.jpg"

class Retezy extends React.Component {
    constructor () {
        super();
        this.state = {
            show: false
        }
    }

    // functions

    handleModal1() {
        this.setState({ show: !this.state.show });
        }


    render() {
    return (

        <Fragment>
            <a id="PiloveRetezy"></a>
            <h3 className="Kapitola-gray">Prodej a opravy pilových řetězů</h3>
            <div className="CardGroup gray-background">
                <Card className="BombCardGroup">
                <Card.Img src={Retez} className="BombCardImg"/>
                <Card.Body className="yellow-background">
                    {/* <Card.Title>Bomby</Card.Title> */}
                    <Card.Text className="BombText black-color">
                    Prodáváme pilové řetězy značky Oregon a opravujeme poškozené řetězové články.
                    </Card.Text>
                </Card.Body>
                <Card.Footer className="yellow">
                    <Button size="lg" variant="danger" onClick={() => {this.handleModal1()}}>Detail</Button>
                </Card.Footer>
                </Card>
            </div>
            <Modal size="lg" centered className="BombModal" show={this.state.show} onHide={() => this.handleModal1()}>
                <Modal.Header closeButton />
                <Modal.Title className="Kapitola-yellow">Prodej a opravy pilových řetězů</Modal.Title>
                <Modal.Body>
                    <p>
                    Vynálezcem pilového řetězu tak, jak jej známe dnes, je Joe Cox z Oregonu v Kanadě. A právě Cox v lese dlouho a detailně pozoroval larvy tesaříka, které mají dvě výrazná a mohutná kusadla. Tyto larvy si velmi efektivně razí tunelové chodby ve dřevě, a to střídavě vždy levým a pravým kusadlem. To inspirovalo Coxe k pokusům s pilovými řetězy se střídavými zuby tak, jak se v podstatě používají dodnes. Joe Cox si nechal tento vynález patentovat a v roce 1947 založil celosvětově známou firmu Oregon. Ta je dnes světovým lídrem ve výrobě a prodeji pilových řetězů. 
                    </p>
                    <br/>
                    <p>
                    A právě proto jsme regionálním prodejcem pilových řetězů značky Oregon. Tyto řetězy jsou velmi kvalitní, mají dlouhou životnost a jsou kompatibilní s naprostou většinou elektrických i benzínových pil. Prodáváme hotové řetězové smyčky a také opravujeme prasklé pilové řetězy formou výměny jejich poškozených článků a novým snýtováním řetězu.
                    </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="lg" variant="danger" onClick={() => {this.handleModal1()}}>Zavřít</Button>
                </Modal.Footer>
            </Modal>

        </Fragment>

        )
    }

}

export default Retezy