import React, { Fragment } from "react";
import "../App.css"
import Carborundum from "../images/logos/Carborundum.png"
import Guede from "../images/logos/Guede.jpg"
import Haffner from "../images/logos/Haffner.png"
import Holzmann from "../images/logos/Holzmann.jpeg"
import Optimum from "../images/logos/Optimum.jpg"
import Oregon from "../images/logos/Oregon.png"
import Proma from "../images/logos/Proma.jpg"
import Samura from "../images/logos/Samura.jpeg"
import Tormek from "../images/logos/Tormek.png"
import Tyrolit from "../images/logos/Tyrolit.png"

function Onas () {
    return(
        <Fragment>
            <div className="gray-background">
                <a id="Onas"></a>
                <h1 className="Kapitola-gray">O nás</h1>
                <div className="Text">
                    
                    <p>Jsme malá rodinná firma, zaměřená především na maloobjemové broušení celé řady ručních nástrojů, nástrojů pro elektrické i benzínové nářadí, a také broušení bruslí. Jsme rovněž vybaveni několika stroji pro drobné kovoobrábění. Můžeme proto nabídnout i výrobu drobných zakázkových dílů, a to primárně z vlastního materiálu zákazníka. Naší další činností je výroba pilových řetězů na míru a nabízíme i opravy poškozených pilových řetězů. Výčet našich služeb uzavírá plnění tlakových lahví, určených pro domácí výrobníky perlivé vody, potravinářským plynem CO<sub>2</sub> .
                    </p>
                    <br/>
                    <p>
                    Tyto činnosti jsou pro nás vedlejší výdělečnou činností a současně i koníčkem. Díky tomu se můžeme soustředit na maximální možnou kvalitu, se zaměřením na individuální přání zákazníka. Hlavní motivací tak pro nás není zisk, ale především radost z precizně odvedené práce a plná spokojenost těch, kteří se na nás s důvěrou obracejí.
                    </p>
                    <br/>
                    <p>
                    Pokud budete s našimi službami spokojeni, pak samozřejmě oceníme, když si tuto kladnou zkušenost nenecháte jen pro sebe. A pokud naopak o kvalitě našich služeb jakkoliv pochybujete, pak Vám budeme vděčni za každý konstruktivní podnět k našemu zlepšení.            
                    </p>
                </div>
                <br/>
                <div className="yellow-background">
                    <br/>
                    <h3>Používáme stroje a originální příslušenství těchto značek:</h3>
                    <br/>
                    <div className="Images">
                        <img className="Logos" src={Haffner} alt="Haffner"/>
                        <img className="Logos" src={Optimum} alt="Optimum"/>
                        <img className="Logos" src={Guede} alt="Guede"/>
                        <img className="Logos" src={Carborundum} alt="Carnotundum"/>
                        <img className="Logos" src={Tyrolit} alt="Tyrolit"/>
                        <img className="Logos" src={Samura} alt="Samura"/>
                        <img className="Logos" src={Proma} alt="Proma"/>
                        <img className="Logos" src={Holzmann} alt="Holzmann"/>
                        <img className="Logos" src={Tormek} alt="Tormek"/>     
                    </div>
                    <br/>
                </div>
                <br/>
                <h3 className="white-color">Zároveň jsme regionálním prodejcem pilových řetězů značky:</h3>
                <br/>
                <img className="Logos" id="Oregon-logo" src={Oregon} alt="Oregon"/>
                <br/>
                <br/>
            </div>
        </Fragment>

    )

}

export default Onas